@import 'src/assets/sass/helpers/_variables';
@import 'src/assets/sass/helpers/_mixins';


.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    background-color: #fff;
    z-index: 5;
    display: flex;
    align-items: center;
    @include r(767) {
        height: 40px; }

    &__container {
        width: 100%;
        max-width: 1170px;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 30px;
        @include r(767) {
            padding: 0 15px; } }

    .nav {
        @include r(1023) {
            display: none; } } }
